<template>
  <div class="yoda">
    <h1>Welcome to YodaBot</h1>
    <div class="container-fluid">
            <div class="row">
                {{apiKey}}
                <div class="col-md-6 offset-md-3">
                    <div class="container-chat">
                        <div class="divForm">
                            <div>
                                <ul v-if="messages.length">
                                    <ListMessages
                                        v-for="msg in messages"
                                        :key="msg.id"
                                        :msg="msg"
                                    />
                                </ul>
                                <input
                                    type="text"
                                    class="input"
                                    v-model="query"
                                >
                                <button v-on:click="search()">Send</button>
                                <div>
                                    {{writting}}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
  </div>
</template>

<script>

import axios from 'axios';
import ListMessages from './ListMessages.vue'

const user = "You";
const yoda = "Yoda"
export default {
	components: {
		ListMessages
	},
  data () {
    return {
            query: '',
            writting:'',
            apiKey:'',
      messages: [
				{
					who: yoda,
                    text: 'Ask me anything'
				}
			]
    }
  },
	methods: {
		search () {
            this.writting = 'writting...'
			const trimmedText = this.query.trim()
			if (trimmedText!='') {
                this.addMessage(user,trimmedText)

                axios
                .get('functions/ajax.php',{
                    params: {
                        query: this.query
                    }
                })
                .then(response => (this.addMessage(yoda,response.data)))
                
            }
            this.writting = '';
            this.query ='';
        },
        addMessage (who,text) {
            this.messages.push({
                who: who,
                text: text
            })
        },
        getAuth(){
            axios
                .post('functions/ajaxGetAuth.php',{
                    params: {
                        query: this.query
                    }
                })
                .then(response => (this.apiKey=response))
        }
    },
    mounted :function () {
            this.getAuth()
    }
}

</script>

<style scoped>
li{
   list-style-type: none; 
}
</style>